
.designText{
    font-family: 'Playfair Display';
    background-color: #fefefe;
    color: #0d0d0d;
    text-align: center;
    font-size: larger;
    padding-top: 2%;
    margin-top: 2%;
}

.crazy3 {
    position: absolute;
    overflow: hidden;
    top: 90;
    bottom: 0;
    left: 70;
    right: 0;
    margin-left: 6%;
    margin-top: 5%;
    width: 44%;
    height: 40%;
    box-shadow: 2px 2px 16px 0px rgba(0,0,0,0.375);
    border-radius: 2px;
    transform: perspective(512px) translate3d(0, 0, 0);
    transform-style: preserve-3d;
    transition: all .4s cubic-bezier(.125, .625, .125, .875);
  }
  .crazy3:hover {
    transform: scale(1.05);
    box-shadow: 4px 4px 24px 0px rgba(0,0,0,0.25);
    border-radius: 4px;
  }
  .crazy3__bg {
    position: relative;
    width: 100%;
    height: 100%;
  
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -2;
  }
  .cover {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: 100%;
  }
  .crazy3__overlay {
  
    transition: all .4s cubic-bezier(.125, .625, .125, .875);
    opacity: 0;
    z-index: -1;
  }
  .crazy3:hover .crazy3__overlay {
    opacity: .625;
  }
  .crazy3__info {
    transform: perspective(512px) translate3d(0, 0, 0);
    transform-style: preserve-3d;
    transition: all .4s cubic-bezier(.125, .625, .125, .875);
    z-index: 1;
  }
  .crazy3__title, .crazy3__cta {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    opacity: 0;
    transform: scale(.875) perspective(512px) translate3d(0, 64px, 64px);
    transition: all .4s cubic-bezier(.125, .625, .125, .875);
  }
  .crazy3__title {
    top: 0;
    width: 256px;
    height: 48px;
    color: #fff;
    font-family: sans-serif;
    font-size: 64px;
    font-weight: lighter;
    line-height: 48px;
    text-align: center;
  }
  .crazy3__cta {
    top: 160px;
    width: 96px;
    height: 32px;
    background: none;
    color: #9B4F96;
    font-size: 14px;
    outline: none;
    border: none;
    box-shadow: none;
    cursor: pointer;
  }
  .crazy3__cta::before,
  .crazy3__cta::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 96px;
    height: 32px;
    border-radius: 2px;
    transition: all .4s cubic-bezier(.125, .625, .125, .875);
    z-index: -1;
  }
  .crazy3__cta::before {
    background: #fff;
    border: 2px solid transparent;
    transform: scale(1.125);
  }
  .crazy3__cta::after {
    background: none;
    border: 2px solid #fff;
    transform: scale(.875);
  }
  .crazy3__cta:hover::before {
    border-radius: 4px;
    transform: scale(.875);
  }
  .crazy3__cta:hover::after {
    border-radius: 4px;
    transform: scale(1.125);
  }
  .ice {
    position: absolute;
    right: 16px;
    opacity: 0;
    transform: translateX(16px);
    transition: all .4s cubic-bezier(.125, .625, .125, .875);
  }
  .crazy3__cta:hover .icon {
    opacity: 1;
    transform: translateX(0);
  }
  .crazy3:hover .crazy3__title,
  .crazy3:hover .crazy3__cta {
    opacity: 1;
    transform: scale(.875) perspective(512px) translate3d(0, -32px, 64px);
  }
